<template>
  <div class="container">
    <div class="chart">
        <div id="myChart" style="width: 100%; height: 100%;"></div>
    </div>
    <div class="row">
      <div class="title leftTitle"><span>当月收入</span><br>{{totalEraning}}.00</div>
      <div @click="" class="add-btn">
        <i>+</i>
      </div>
      <div class="title rightTitle"><span>当月支出</span><br>{{totalExpense}}.00</div>       
    </div>
    <div v-show="detailShow" class="detail" transition="fade">
      <div class="detail-main">
        <input class="calendarBtn" readonly="readonly" size="11" type="text" v-model="calendar.items.text" @click.stop="open($event)" placeholder="请选择日期…" />
        <Calendar :show="calendar.show" :type="calendar.items.type" :value="calendar.items.value" :x="calendar.x" :y="calendar.y"
            :begin="calendar.items.begin" @cancel="dateCancel" @ok="dateOk" :end="calendar.items.end" :sep="calendar.items.sep"
            :single="calendar.items.single" :rangeValue='calendar.items.rangeValue' :autoclose='calendar.items.autoclose'>
        </Calendar>
        <span class="weightShow">{{newWeight}}</span><span class="weightKg">&nbsp;kg</span>
        <i class="detail-add" @click="addWeight">+</i>
      </div>
      <div class="detail-input">
        <div class="detail-line">
          <button @click="demo(1,1)">1</button>
          <button @click="demo(1,2)">2</button>
          <button @click="demo(1,3)">3</button>
        </div>
        <div class="detail-line">
          <button @click="demo(1,4)">4</button>
          <button @click="demo(1,5)">5</button>
          <button @click="demo(1,6)">6</button>
        </div>
        <div class="detail-line">
          <button @click="demo(1,7)">7</button>
          <button @click="demo(1,8)">8</button>
          <button @click="demo(1,9)">9</button>
        </div>
        <div class="detail-line">
          <button @click="demo(2,null)">C</button>
          <button @click="demo(1,0)">0</button>
          <button @click="demo(3,null)">.</button>
        </div>
      </div>
      <div class="detail-close" @click="hideDetail">
        <i class="icon-close">×</i>
      </div>
    </div>
  </div>
</template>

<script>
import Store from '@/localstorage'
import Calendar from './calendar'
var myChart
var thisMonth = new Date().getMonth() >= 9 ? (new Date().getMonth() + 1).toString() : '0' + (new Date().getMonth() + 1)
var thisDay = new Date().getDate() >= 9 ? (new Date().getDate() + 1).toString() : '0' + (new Date().getDate() + 1)
export default {
  components: {
    Calendar
  },
  data () {
    return {
      totalEraning: 14,
      totalExpense: 20,
      msg: 'fit',
      chart: null,
      detailShow: false,
      newWeight: '',
      inputValue: 0,
      weight: Store.fetch('notebook-fit') || [],
      calendar: {
        show: false,
        x: 0,
        y: 0,
        items: {
          type: 'date',
          begin: '2015/08/20',
          end: [new Date().getFullYear(), thisMonth, thisDay].join('/'),
          value: [new Date().getFullYear(), thisMonth, thisDay].join('/'),
          text: [new Date().getFullYear(), thisMonth, thisDay].join('/'),
          sep: '/',
          single: true,
          autoclose: true,
          weeks: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
          months: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
        }
      },
      option: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'value'
          }
        ],
        yAxis: [
          {
            type: 'category',
            axisTick: {show: false},
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
          }
        ],
        // yAxis: {
        //   show: false,
        //   type: 'time',
        //   splitLine: {
        //     show: false
        //   }
        // },
        series: [
          {
            name: '直接访问',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: [320, 302, 301, 334, 390, 330, 320]
          },
          {
            name: '邮件营销',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '联盟广告',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: '视频广告',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: [-150, -212, -201, -154, -190, -330, -410]
          },
          {
            name: '搜索引擎',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: [820, 832, 901, 934, 1290, 1330, 1320]
          }
        ]
      }
    }
  },
  mounted () {
    myChart = this.$echarts.init(document.getElementById('myChart'))
    myChart.setOption(this.option)
    myChart.setOption({
      series: [{
        data: this.weight
      }]
    })
  },
  methods: {
    showDetail () {
      this.detailShow = true
    },
    hideDetail () {
      this.detailShow = false
      this.newWeight = ''
    },
    addWeight () {
      this.inputValue = parseFloat(this.newWeight)
      console.log(typeof this.calendar.items.value, this.calendar.items.value)
      if (isNaN(this.inputValue)) {
        this.detailShow = false
        this.newWeight = ''
      } else {
        this.weight.push([this.calendar.items.value, this.inputValue])
        myChart.setOption({
          series: [{
            data: this.weight.sort()
          }]
        })
        this.detailShow = false
        this.newWeight = ''
      }
    },
    demo (tip, value) {
      if (tip === 1) {
        if ((value !== 0 || this.newWeight.length !== 0) && this.newWeight.indexOf('.') !== this.newWeight.length - 4) {
          this.newWeight = this.newWeight + value.toString()
        }
      } else if (tip === 2) {
        this.newWeight = ''
      } else if (tip === 3) {
        if (this.newWeight.indexOf('.') === -1) {
          this.newWeight = this.newWeight + '.'
        }
      }
    },
    open (e, type) {
      // 设置类型
      this.calendar.show = true
      this.calendar.x = e.target.offsetLeft
      this.calendar.y = e.target.offsetTop + e.target.offsetHeight + 8
    },
    dateCancel (obj) {
      this.calendar.show = obj.show
    },
    dateOk (obj) {
      this.calendar.items.text = obj.values[0]
      this.calendar.items.value = obj.values[0]
    }
  },
  watch: {
    weight: {
      handler: function (weight) {
        Store.save('notebook-fit', weight)
      },
      deep: true
    }
  }
}
</script>

<style scoped>
.container {
  color: black;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}
.add-btn {
  position: absolute;
  margin: 5px 45% 0 45%;
  font-style: normal; 
  width: 60px;
  height: 60px;
  color:rgba(0, 111, 111, 0.5);
  background: rgba(255,255,255, 0.6);
  text-align: center;
  font-family: 'Times New Roman';
  line-height: 60px; 
  font-size: 4.5em;
  font-weight: bolder;
  border-radius: 30px 30px;
}
.title {
  font-style: normal; 
  text-align: left;
  width: 100px;
  color: #FFF;
  font-size: 28px;
  line-height: 28px;
}
.title span {
  color: #0276b1;
  font-size: 15px;
}
.leftTitle {
  float: left;
  text-align: left;
  margin-left: 5px;
}
.rightTitle {
  float: right;
  text-align: right;
  margin-right: 5px;
}
.chart {
  width: 100%;
  height: 80%;
  overflow-x: hidden;
  overflow-y: scroll;
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
}
.detail {
  position: relative;
  color: #fff;
  background: rgba(49, 78, 95, .95);
  width: 100%;
  min-height: 6000px;
}
.detail-main {
  height: 100px;
  line-height: 100px;
  width: 100%;
}
.weightShow {
  position: absolute;
  top: 25px;
  left: 35%;
  width: 100px;
  height: 40px;
  font-size: 1.8em;
  text-align: center;
  color: #0276b1;
  padding-bottom: 28px;
  border-bottom: thick dotted #5e7a88;
}
.weightKg {
  position: absolute;
  top: 25px;
  left: 62%;
  height: 40px;
  font-size: 1.8em;
  text-align: center;
  color: #5e7a88;
}
.detail-add {
  position: absolute;
  top: 20px;
  right: 20px;
  font-style: normal; 
  width: 40px;
  height: 40px;
  color: rgba(49, 78, 95, .95);
  background: rgba(255,255,255, 0.8);
  font-family: 'Times New Roman';
  line-height: 40px; 
  font-size: 3em;
  font-weight: bolder;
  border-radius: 20px 20px;
}
.detail-input {
  width: 80%;
  margin: 0 auto;
}
.detail-line{
  width: 100%;
  display: flex;
  justify-content: center;
}
button {
  display: block;
  flex: 33%;
  font-size: 26px;
  padding-top: 20px;
  color: rgba(255,255,255, 1);
  background-color: transparent;
  border-style: none;
  outline:none;
}
button:hover {
  color: #0276b1;
}
.detail-close {
  clear: both;
  font-size: 32px;
}
.icon-close {
  margin: 0 auto;
  margin-top: 8%;
  margin-left: 44%;
  float: left;
  font-style: normal; 
  width: 40px;
  height: 40px;
  color: rgba(49, 78, 95, .95);
  background: rgba(255,255,255, 0.8);
  text-align: center;
  font-family: 'Times New Roman';
  line-height: 40px; 
  font-size: 1.5em;
  font-weight: bolder;
  border-radius: 20px 20px;
}
.calendarBtn{
  position: absolute;
  top: 1px;
  left: 10px;
  height: 40px;
  font-style: normal; 
  border-radius: 2px;
  padding:5px;
  font-size: 1.3em;
  background:url(data:image/svg+xml;base64,) no-repeat 8px 17px;
  padding-left: 35px;
  color: rgba(255,255,255, 1);
}
</style>



